.input-bar {
  background-color: white;
  border-radius: 20px;
  width: 40%;
}

.new-task {
  height: 100px;
  padding: 20px 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
}

.input-style {
  border: none;
  text-align: center;
  font-size: 1.2rem;
  padding-bottom: 2px;
  border-bottom: 3px solid black;
  border-radius: 5px;
  width: 70%;
}

.add-button {
  cursor: pointer;
  border: none;
  background-color: black;
  color: white;
  width: 25%;
  height: 30px;
  border-radius: 5px;
  transition: 0.3s;
}

.add-button:hover {
    transform: scale(1.05);
}
