.DisplayTask {
    width: 40%;
    background-color: white;
    border-radius: 30px;
    padding: 13px;
    display: flex;
    gap: 20px;
    animation: color-change 4s infinite linear;
  }
  
  .DisplayTask {
    background: linear-gradient(270deg, #f6cfb6, #f4abab, #bfc4f4, #fbd6f6, #d3f5bd);
    background-size: 1000% 1000%;

    -webkit-animation: AnimationName 3s ease infinite;
    -moz-animation: AnimationName 3s ease infinite;
    -o-animation: AnimationName 3s ease infinite;
    animation: AnimationName 3s ease infinite;
}

@-webkit-keyframes AnimationName {
    0%{background-position:0% 58%}
    50%{background-position:100% 43%}
    100%{background-position:0% 58%}
}
@-moz-keyframes AnimationName {
    0%{background-position:0% 58%}
    50%{background-position:100% 43%}
    100%{background-position:0% 58%}
}
@-o-keyframes AnimationName {
    0%{background-position:0% 58%}
    50%{background-position:100% 43%}
    100%{background-position:0% 58%}
}
@keyframes AnimationName {
    0%{background-position:0% 58%}
    50%{background-position:100% 43%}
    100%{background-position:0% 58%}
}


.check-box {
    margin-left: 20px;
    position: relative;
    top: 2.5px;
    width: 18px; /* Desired width */
    height: 18px; /* Desired height */
    -webkit-appearance: none; /* Disable default appearance */
    -moz-appearance: none;
    appearance: none;
    border: 2px solid black; /* Checkbox border color */
    border-radius: 4px; /* Checkbox border radius */
    outline: none; /* Remove focus outline */
    background-color: rgba(0, 0, 0, 0); /* Checkbox background color */
  }
  
  .check-box::before {
    content: "\2713"; /* Unicode character for checkmark */
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-weight: 1000;
    font-size: 14px; /* Adjust the size of the checkmark */
    color: rgb(0, 0, 0); /* Color of the checkmark */
    opacity: 0; /* Initially invisible */
  }
  
  .check-box:checked::before {
    opacity: 1; /* Show the checkmark when the checkbox is checked */
  }
  
  
  
  

.task-style {
    font-size: 1.4rem;
    text-transform: capitalize;
    font-family: 'Courier New', Courier, monospace;
    font-weight: 1000;
}
